<template>
  <div class="Header">
    <Menu mode="horizontal" theme="light"
            :active-name="$route.name"
            @on-select="$router.push({ name: $event, params: {id: $store.state.auth.authUser.id} })"
            :default-active="$route.name" :accordion=true
            style="width: 100%; min-width: 700px; position: relative;">
      <div class="layout-nav">
        <MenuItem v-for="(item, idx) in menu" :name="item.to" :key="idx">
          <Icon :type="item.icon"></Icon>
          {{item.title}}
        </MenuItem>
      </div>
      <div style="float: right; margin-right: 40px;">
        <div v-if="username">
          <Avatar :src="getHash(email)" size="large" shape="square" class="avatar"></Avatar>
          <span class="Username">{{ username }}</span>
          <Button type="error" @click="LogOut()" style="margin-left: 20px;">退出登录</Button>
        </div>
        <div v-else>
          <Button type="primary" @click="ToLogin()">登录</Button>
          <Button @click="ToRegister()">注册</Button>
        </div>
      </div>
    </Menu>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      menu: [
        {
          title: '首页',
          icon: 'home',
          to: 'Index'
        },
        {
          title: '图书检索',
          icon: '',
          to: 'Select'
        },
        {
          title: '个人信息',
          icon: '',
          to: 'UserInfo'
        },
        {
          title: '我要开店',
          icon: '',
          to: 'NewBookSeller'
        }
      ],
      username: '',
      email: '',
      avatar: ''
    }
  },
  computed: {
  },
  beforeMount: function () {
    this.token = this.$store.state.auth.token
    this.userId = this.$store.state.auth.authUser
    this.$http.get('user/' + this.userId, {
      params: {
        token: this.token
      }
    })
      .then(r => {
        this.username = r.data.username
        this.email = r.data.email
        if (r.data.role.alias === 'bookseller') {
          this.menu[3] = {
            title: '我的店铺',
            icon: '',
            to: 'DashboardBookSeller'
          }
        }
      })
      .catch(e => {
      })
  },
  methods: {
    ToLogin () {
      this.$Loading.start()
      this.$Loading.finish()
      this.$router.push({ name: 'Login' })
    },
    ToRegister () {
      this.$Loading.start()
      this.$Loading.finish()
      this.$router.push({ name: 'Register' })
    },
    LogOut () {
      event.preventDefault()
      localStorage.clear()
      this.$store.commit('SET_API_TOKEN', '')
      this.$store.commit('SET_AUTH_USER', '')
      this.token = ''
      this.userId = ''
      this.username = ''
      this.$router.push({ name: 'Index' })
    },
    getHash (email) {
      let hashCode = '' + email
      hashCode = hashCode.replace(/^\s+|\s+$/g, '').toLowerCase()
      let createHash = require('create-hash')
      let hash = createHash('md5')
      hash.update(hashCode)
      hashCode = hash.digest('HEX')
      return 'http://www.gravatar.com/avatar/' + hashCode + '?d=retro'
    }
  }
}
</script>

<style scoped>
  .Header {
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
</style>



